.fixed{
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: 0;
  z-index: 6;
}
.column{
  float: left;
  padding: 0px;
}
#makeFloat{
  width: 35%;
  float:left;
}
#profile{
  height: 60vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;

}
.bottomProfile{
  height: 40vh;
  text-align: center;
}
#bigText{
  font-size: 120%;
}
#myface {
  height: 99%;
  float: left;
  padding-left: 1%;
}
#nameMajor{
  font-size: 400%;
  padding-right:10%;
  text-align: center;
}
#schoolInfo{
  font-size: 150%;
}

.menuBar{
  width: 10%;
  height: 99vh;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -ms-flex-align: center;
      align-items: center;
  padding-top: 2%;
  padding-bottom: 2%;
}

.button {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
  height:20%;
  border: 2px solid #C01814;
  border-radius: 5%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 155%;
  text-decoration: none;
  font-weight: 300;
  text-align: center;
  -webkit-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;
}
.button span {
  -ms-flex: 1 1;
      flex: 1 1;
}
.button:hover {
  background-color: #C01814;
  color: white;
}
.button:active {
  -webkit-transform: translateY(4px);
      -ms-transform: translateY(4px);
          transform: translateY(4px);
}
.button.selected{
  background-color: #C01814;
  color: white;
}

.arrowBar{
  width: 5%;
  height: 100vh;
}

.infoBar{
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  width: 45%;
  height: 99vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align:center;
      align-items:center;
  background-color: #FFF;
  z-index: 3;
}
#displayBorder{
  position: fixed;
  top: 0;
  right: 0;
  z-index: 5;
  border-radius: 5%;
  margin-right: 5%;
  width: 44%;
  margin-top: 4%;
  height: 84%;
  border: 3px solid #C01814;
  padding: 5%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-box-shadow: 0 0 0 9999px #FFF;
          box-shadow: 0 0 0 9999px #FFF;
}
#displayBox{
  background-color: #FFF6F6;
  position: static;
  font-size: 120%;
  padding-top: 11%;
  padding-bottom: 20%;
  padding-left: 15%;
  width: 100%;
  z-index: -1;

}

.row:after {
    content: "";
    display: table;
    clear: both;
}
@media screen and (max-width: 1000px) {
    .column {
        width: 100%;
    }
}



